import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  G_Token: localStorage.getItem("G_Token")
    ? localStorage.getItem("G_Token")
    : null,
  G_Info: localStorage.getItem("G_Info")
    ? JSON.parse(localStorage.getItem("G_Info"))
    : null,
};

const UserSlice = createSlice({
  name: "user",
  initialState,
  reducers: {
    G_TokenSlice: (state, action) => {
      state.G_Token = action.payload;
      localStorage.setItem("G_Token", action.payload);
    },
    G_InfoSlice: (state, action) => {
      state.G_Info = action.payload;
      localStorage.setItem("G_Info", JSON.stringify(action.payload));
    },
  },
});

export const { G_TokenSlice, G_InfoSlice } = UserSlice.actions;
export default UserSlice.reducer;
