import React, { useEffect, useState } from "react";
import {
  Menu,
  MenuItem,
  ProSidebar,
  SidebarContent,
  SidebarFooter,
  SidebarHeader,
} from "react-pro-sidebar";
import "react-pro-sidebar/dist/css/styles.css";
import { NavLink } from "react-router-dom";
import ThreeLineIcon from "../../Assets/Images/ThreeLineIcon";
import YoutubeIcon from "../../Assets/Images/YoutubeIcon";
import { MaildetailSlice } from "../../ReduxToolkit/EmailSlice";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import InboxIcon from "../../Assets/Images/InboxIcon";
import StarIcon from "../../Assets/Images/StarIcon";
import SentIcon from "../../Assets/Images/SentIcon";
import DraftIcon from "../../Assets/Images/DraftIcon";
import SpamIcon from "../../Assets/Images/SpamIcon";
import TrashIcon from "../../Assets/Images/TrashIcon";
import Importanticon from "../../Assets/Images/Importanticon";
import { Button } from "react-bootstrap";
import SendMail from "../../Modal/SendMail";
import ComposeIcon from "../../Assets/Images/ComposeIcon";
import AllmailIcon from "../../Assets/Images/AllmailIcon";

const Sidebar = (props) => {
  const AllCount = useSelector((state) => state.emaildata.AllCount);
  const [activeTab, setActiveTab] = useState("");

  useEffect(() => {
    if (props?.URLfromHeaderTOSidebar !== undefined) {
      setActiveTab(props?.URLfromHeaderTOSidebar);
    }
  }, [props?.URLfromHeaderTOSidebar]);

  useEffect(() => {
    if (window.location.href) {
      let temp = window.location.href.split("/");
      setActiveTab(temp[temp?.length - 1]);
    }
  }, [activeTab]);

  const [SendMailModalshow, setSendMailModalshow] = useState(false);

  let dispatch = useDispatch();
  const [menuCollapse, setMenuCollapse] = useState(false);
  const menuIconClick = () => {
    menuCollapse ? setMenuCollapse(false) : setMenuCollapse(true);
  };


  return (
    <>
      <div id="header">
        <ProSidebar collapsed={menuCollapse}>
          <SidebarHeader>
            <div className="closemenu" onClick={menuIconClick}>
              {/* changing menu collapse icon on click */}
              {/* {menuCollapse ? "close" : "open"} */}
              <ThreeLineIcon />
            </div>
          </SidebarHeader>
          <SidebarContent>
            <Menu iconShape="square">
              {/* <MenuItem
                active={activeTab === "inbox" ? true : false}
                icon={<InboxIcon />}
              >
                <NavLink
                  to="/inbox"
                  onClick={() => {
                    setActiveTab("inbox");
                    dispatch(MaildetailSlice(null));
                  }}
                />
                {!menuCollapse && (
                  <div className="d-flex justify-content-between">
                    <div>Inbox</div>
                    <div>
                      {TotalSInboxEmailCount > 0 ? TotalSInboxEmailCount : ""}
                    </div>
                  </div>
                )}
              </MenuItem> */}

              <Button
                className="compose-btn mb-3"
                onClick={() => setSendMailModalshow(true)}
              >
                <ComposeIcon />
                {!menuCollapse && <span className="ms-3">Compose</span>}
              </Button>
              <MenuItem
                active={activeTab === "inbox" ? true : false}
                icon={<InboxIcon />}
              >
                <NavLink
                  to="/inbox"
                  onClick={() => {
                    setActiveTab("inbox");
                    dispatch(MaildetailSlice(null));
                  }}
                />
                {!menuCollapse && (
                  <div className="d-flex justify-content-between">
                    <div>Inbox</div>
                    <div>{AllCount?.inbox > 0 ? AllCount?.inbox : ""}</div>
                  </div>
                )}
              </MenuItem>
              <MenuItem
                active={activeTab === "starred" ? true : false}
                icon={<StarIcon />}
              >
                <NavLink
                  to="/starred"
                  onClick={() => {
                    setActiveTab("starred");
                    props.OnLogoClick("starred")
                    dispatch(MaildetailSlice(null));
                  }}
                />
                {!menuCollapse && (
                  <div className="d-flex     justify-content-between">
                    <div>Starred</div>
                    <div>{AllCount?.starred > 0 ? AllCount?.starred : ""}</div>
                  </div>
                )}
              </MenuItem>
              <MenuItem
                active={activeTab === "sent" ? true : false}
                icon={<SentIcon />}
              >
                <NavLink
                  to="/sent"
                  onClick={() => {
                    setActiveTab("sent");
                    props.OnLogoClick("sent")
                    dispatch(MaildetailSlice(null));
                  }}
                />
                {!menuCollapse && (
                  <div className="d-flex     justify-content-between">
                    <div>Sent</div>
                    <div>{AllCount?.sent > 0 ? AllCount?.sent : ""}</div>
                  </div>
                )}
              </MenuItem>
              <MenuItem
                active={activeTab === "drafts" ? true : false}
                icon={<DraftIcon />}
              >
                <NavLink
                  to="/drafts"
                  onClick={() => {
                    setActiveTab("drafts");
                    props.OnLogoClick("drafts")
                    dispatch(MaildetailSlice(null));
                  }}
                />
                {!menuCollapse && (
                  <div className="d-flex     justify-content-between">
                    <div>Drafts</div>
                    <div>{AllCount?.draft > 0 ? AllCount?.draft : ""}</div>
                  </div>
                )}
              </MenuItem>
              <MenuItem
                active={activeTab === "important" ? true : false}
                icon={<Importanticon />}
              >
                <NavLink
                  to="/important"
                  onClick={() => {
                    setActiveTab("important");
                    props.OnLogoClick("important")
                    dispatch(MaildetailSlice(null));
                  }}
                />
                {!menuCollapse && (
                  <div className="d-flex     justify-content-between">
                    <div>Important</div>
                    <div>
                      {AllCount?.important > 0 ? AllCount?.important : ""}
                    </div>
                  </div>
                )}
              </MenuItem>
              <MenuItem
                active={activeTab === "spam" ? true : false}
                icon={<SpamIcon />}
              >
                <NavLink
                  to="/spam"
                  onClick={() => {
                    setActiveTab("spam");
                    props.OnLogoClick("spam")
                    dispatch(MaildetailSlice(null));
                  }}
                />
                {!menuCollapse && (
                  <div className="d-flex     justify-content-between">
                    <div>Spam</div>
                    <div>{AllCount?.spam > 0 ? AllCount?.spam : ""}</div>
                  </div>
                )}
              </MenuItem>
              <MenuItem
                active={activeTab === "trash" ? true : false}
                icon={<TrashIcon />}
              >
                <NavLink
                  to="/trash"
                  onClick={() => {
                    setActiveTab("trash");
                    props.OnLogoClick("trash")
                    dispatch(MaildetailSlice(null));
                  }}
                />
                {!menuCollapse && (
                  <div className="d-flex     justify-content-between">
                    <div>Trash</div>
                    <div>{AllCount?.trash > 0 ? AllCount?.trash : ""}</div>
                  </div>
                )}
              </MenuItem>
              <MenuItem
                active={activeTab === "all" ? true : false}
                icon={<AllmailIcon />}
              >
                <NavLink
                  to="/all"
                  onClick={() => {
                    setActiveTab("all");
                    props.OnLogoClick("all")
                    dispatch(MaildetailSlice(null));
                  }}
                />
                {!menuCollapse && (
                  <div className="d-flex     justify-content-between">
                    <div>All Mail</div>
                    <div>{AllCount?.all > 0 ? AllCount?.all : ""}</div>
                  </div>
                )}
              </MenuItem>
            </Menu>
          </SidebarContent>
          {/* <SidebarFooter>
            <Menu iconShape="square">
              <MenuItem 
             icon={<YoutubeIcon />}
              >Logout</MenuItem>
            </Menu>
          </SidebarFooter> */}
        </ProSidebar>
      </div>
      {SendMailModalshow && (
        <SendMail
          show={SendMailModalshow}
          onHide={() => setSendMailModalshow(false)}
        />
      )}
    </>
  );
};

export default Sidebar;
