import axios from "axios";
import React, { useEffect, useState } from "react";
import { Button, Col, Row } from "react-bootstrap";
import ProfileMask from "../../Assets/Images/profile_mask2.png";
import { useDispatch, useSelector } from "react-redux";
import { MaildetailSlice } from "../../ReduxToolkit/EmailSlice";
import moment from "moment";
import { Commonservice } from "../../Services/Commonservice";
import ImageIcon from "../../Assets/Images/ImageIcon";
import DownloadIcon from "../../Assets/Images/DownloadIcon";
import Pdficon from "../../Assets/Images/Pdficon";
import AudioIcon from "../../Assets/Images/AudioIcon";
import Textfie from "../../Assets/Images/Textfie";
import Jsonfile from "../../Assets/Images/Jsonfile";
import PPTicon from "../../Assets/Images/PPTicon";
import WordIcon from "../../Assets/Images/WordIcon";
import HtmlIcon from "../../Assets/Images/HtmlIcon";

const MailDetailBox = (props) => {
  let dispatch = useDispatch();
  const G_Token = useSelector((state) => state.userdata.G_Token);
  const UserInfo = useSelector((state) => state.userdata.G_Info);
  const [AttachmentShow, setAttachmentShow] = useState(false);
  const [emailHtml, setEmailHtml] = useState([]);
  const [attachments, setAttachments] = useState([]);
  const getEmailSender = (value) => {
    const startIndex = value?.indexOf("<");
    const endIndex = value?.indexOf(">");
    if (startIndex !== -1 && endIndex !== -1) {
      return value?.substring(0, startIndex)?.trim();
    } else {
      return value.trim();
    }
  };
  async function fetchAttachment(EmailID, element) {
    try {
      const response = await axios.get(
        `https://www.googleapis.com/gmail/v1/users/${UserInfo?.email}/messages/${EmailID}/attachments/${element.body.attachmentId}`,
        {
          headers: { Authorization: `Bearer ${G_Token}` },
          responseType: "arraybuffer",
        }
      );
      const base64Data = btoa(
        new Uint8Array(response.data).reduce(
          (data, byte) => data + String.fromCharCode(byte),
          ""
        )
      );
      const decoded = atob(base64Data);

      // Determine the attachment type based on MIME type
      let attachmentType;
      if (element.mimeType.startsWith("image/")) {
        attachmentType = "image";
      } else if (element.mimeType === "application/pdf") {
        attachmentType = "pdf";
      } else if (
        element.mimeType ===
        "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
      ) {
        attachmentType = "spreadsheet";
      } else if (element.mimeType === "text/html") {
        attachmentType = "html";
      } else if (element.mimeType === "audio/mpeg") {
        attachmentType = "audio";
      } else if (element.mimeType === "video/mp4") {
        attachmentType = "video";
      } else if (element.mimeType === "text/plain") {
        attachmentType = "plain";
      } else if (element.mimeType === "application/json") {
        attachmentType = "json";
      } else if (
        element.mimeType === "application/vnd.oasis.opendocument.text"
      ) {
        attachmentType = "odt";
      } else if (
        element.mimeType ===
        "application/vnd.openxmlformats-officedocument.presentationml.presentation"
      ) {
        attachmentType = "ppt";
      } else {
        // Handle other types or unknown types as needed
        attachmentType = "other";
      }

      // Create an object representing the attachment
      const attachmentData = {
        type: attachmentType,
        data: JSON.parse(decoded),
        name: element.filename,
        mimeType: element.mimeType,
      };

      setAttachments((prevAttachments) => [...prevAttachments, attachmentData]);
      setAttachmentShow(true);
    } catch (error) {
      Commonservice.Logout401(error?.response?.status);
      console.error("Error fetching attachment data:", error);
      setAttachmentShow(false);
    }
  }
  async function processParts(parts, EmailID) {
    for (let i = 0; i < parts.length; i++) {
      const element = parts[i];
      if (element.mimeType === "multipart/mixed") {
        // If the part is "multipart/mixed," process its subparts recursively
        await processParts(element.parts);
      } else if (
        element.mimeType === "text/html" ||
        element.mimeType === "text/x-amp-html"
      ) {
        try {
          const encodedHtml = element.body.data;
          const decodedHtml = atob(
            encodedHtml.replace(/-/g, "+").replace(/_/g, "/")
          );
          const cleanedHtml = decodedHtml.replace(/[^\x20-\x7E]/g, "");

          // setEmailHtml(cleanedHtml);
          setEmailHtml((prev) => [
            ...prev,
            {
              messagesId: EmailID,
              data: cleanedHtml,
            },
          ]);
        } catch (error) {
          console.error("Error decoding HTML data:", error);
        }
      } else if (element.mimeType === "multipart/alternative") {
        let htmlb = element.parts.filter((f) => f.mimeType === "text/html");
        let Imgb = element.parts.filter((f) => f.mimeType.startsWith("image/"));
        try {
          const encodedHtml = htmlb[0].body.data;
          const decodedHtml = atob(
            encodedHtml.replace(/-/g, "+").replace(/_/g, "/")
          );
          const cleanedHtml = decodedHtml.replace(/[^\x20-\x7E]/g, "");
          // setEmailHtml(cleanedHtml);
          setEmailHtml((prev) => [
            ...prev,
            {
              messagesId: EmailID,
              data: cleanedHtml,
            },
          ]);
        } catch (error) {
          console.error("Error decoding HTML data:", error);
        }
        if (Imgb.length > 0) {
          Imgb.forEach((element) => {
            fetchAttachment(props.EmailData.id, element);
          });
        }
      } else if (element.mimeType === "multipart/related") {
        let Malter = element.parts.filter((f) =>
          f.mimeType.startsWith("multipart/alternative")
        );

        Malter.forEach((element) => {
          if (Malter.length > 0) {
            let htmlb = element.parts.filter((f) => f.mimeType === "text/html");
            let Imgb = element.parts.filter((f) =>
              f.mimeType.startsWith("image/")
            );
            try {
              const encodedHtml = htmlb[0].body.data;
              const decodedHtml = atob(
                encodedHtml.replace(/-/g, "+").replace(/_/g, "/")
              );
              const cleanedHtml = decodedHtml.replace(/[^\x20-\x7E]/g, "");
              // setEmailHtml(cleanedHtml);

              setEmailHtml((prev) => [
                ...prev,
                {
                  messagesId: EmailID,
                  data: decodedHtml,
                },
              ]);
              // setEmailHtml(decodedHtml);
            } catch (error) {
              console.error("Error decoding HTML data:", error);
            }
            if (Imgb.length > 0) {
              Imgb.forEach((element) => {
                fetchAttachment(props.EmailData.id, element);
              });
            }
          }
        });

        let htmlb = element.parts.filter((f) => f.mimeType === "text/html");
        let Imgb = element.parts.filter((f) => f.mimeType.startsWith("image/"));

        try {
          const encodedHtml = htmlb[0].body.data;
          const decodedHtml = atob(
            encodedHtml.replace(/-/g, "+").replace(/_/g, "/")
          );
          const cleanedHtml = decodedHtml.replace(/[^\x20-\x7E]/g, "");
          // setEmailHtml(cleanedHtml);
          // setEmailHtml(decodedHtml);
          setEmailHtml((prev) => [
            ...prev,
            {
              messagesId: EmailID,
              data: cleanedHtml,
            },
          ]);
        } catch (error) {
          console.error("Error decoding HTML data:", error);
        }
        if (Imgb.length > 0) {
          Imgb.forEach((element) => {
            fetchAttachment(props.EmailData.id, element);
          });
        }
      } else {
        fetchAttachment(props.EmailData.id, element);
      }
    }
  }

  useEffect(() => {
    if (props.EmailData && props.EmailData.id) {
      props.EmailData.messages.forEach((element) => {
        const parts = element.payload.parts;
        if (
          parts !== undefined ||
          element.payload.mimeType === "multipart/mixed"
        ) {
          processParts(parts, element?.id);
        }

        if (element.payload.mimeType === "text/html") {
          try {
            const encodedHtml = element.payload.body.data;
            const decodedHtml = atob(
              encodedHtml.replace(/-/g, "+").replace(/_/g, "/")
            );
            const cleanedHtml = decodedHtml.replace(/[^\x20-\x7E]/g, "");
            // setEmailHtml(cleanedHtml);
            setEmailHtml((prev) => [
              ...prev,
              {
                messagesId: props.EmailData.id,
                data: cleanedHtml,
              },
            ]);
            setAttachmentShow(true);
          } catch (error) {
            console.error("Error decoding HTML data:", error);
          }
        }

        if (element.payload.mimeType === "text/plain") {
          try {
            const encodedHtml = element.payload.body.data;
            const decodedHtml = atob(
              encodedHtml.replace(/-/g, "+").replace(/_/g, "/")
            );
            const cleanedHtml = decodedHtml.replace(/[^\x20-\x7E]/g, "");
            // setEmailHtml(decodedHtml);
            setEmailHtml((prev) => [
              ...prev,
              {
                messagesId: props.EmailData.id,
                data: decodedHtml,
              },
            ]);
            setAttachmentShow(true);
          } catch (error) {
            setAttachmentShow(false);
            console.error("Error decoding HTML data:", error);
          }
        }
      });
    }
  }, [props.EmailData]);

  function fnImg(value) {
    var imgMain = document.getElementsByTagName("img");
    if (imgMain.length > 0) {
      Array.from(imgMain).forEach((element) => {
        if (element.alt === value.name) {
          element.src = `data:${value.mimeType};base64,${value.data.data
            .replace(/-/g, `+`)
            .replace(/_/g, `/`)}`;
        }
      });
    }

    if (emailHtml !== undefined) {
      // debugger;
      // let contentOne = document.getElementsByClassName("htmlcontentemail");
      // console.log("contentOne ::", contentOne);
      // if (contentOne.length > 0) {
      //   for (let i = 0; i < contentOne.length; i++) {
      //      contentOne[i].innerHTML = contentOne[i].innerHTML.replace("Â", "");
      //     // contentOne[i].innerHTML = contentOne[i].innerHTML.replace(/[Â^a-zA-Z0-9]/g, "");
      //   }
      // }
    }
  }
  function fnBackbtn() {
    dispatch(MaildetailSlice(null));
    localStorage.removeItem("Maildetail");
  }
  
  return (
    <>
      <div className="mail-details">
        <div className="justify-content-start d-flex btn-heading pe-3 p-2     align-items-center">
          <Button onClick={() => fnBackbtn()} variant="primary me-2 np-btn">
            <svg
              fill="rgb(95, 99, 104)"
              width="80px"
              height="80px"
              viewBox="-78.5 0 512 512"
              xmlns="http://www.w3.org/2000/svg"
              {...props}
            >
              <title>{"left"}</title>
              <path d="M257 64L291 98 128 262 291 426 257 460 61 262 257 64Z" />
            </svg>{" "}
            {/* Previous */}
          </Button>
          <h5 className="m-0">{props?.EmailData?.subject?.value}</h5>
        </div>

        <div className="mail-details-body">
          {props?.EmailData?.messages?.map((itemem, indexem) => {
            let from = itemem.payload?.headers?.find(
              (item, i) => item?.name === "From"
            );
            let To = itemem.payload?.headers?.find(
              (item, i) => item?.name === "To"
            );
            let DateTime = itemem.payload?.headers?.find(
              (item, i) => item?.name === "Date"
            );
            let msgDet = emailHtml?.find(
              (data) => data?.messagesId === itemem?.id
            );

            return (
              <>
                <div className="email-detail-heading  d-flex  justify-content-between    align-items-center">
                  <div className="email-detail-heading-profile w-100 d-flex     justify-content-between    align-items-center">
                    <div className="profileName">
                      {getEmailSender(from?.value)[0] === '"'
                        ? getEmailSender(from?.value)[1]
                        : getEmailSender(from?.value)[0]}
                    </div>
                    <div className="px-2 profileNamename">
                      <span className="ms-2">
                        <b>{getEmailSender(from?.value)}</b>
                      </span>
                      <span className="ms-2">
                        <b>To :</b> {To?.value}
                      </span>
                    </div>
                    <div className="email-detail-heading-time">
                      {Commonservice.getDateTimeFormatMailDetail(
                        DateTime?.value
                      )}
                    </div>
                  </div>
                </div>
                <div
                  className="email-detail-content"
                  dangerouslySetInnerHTML={{ __html: msgDet?.data }}
                ></div>
              </>
            );
          })}

          {AttachmentShow ? (
            <div>
              {/* <div
                className="htmlcontentemail"
                dangerouslySetInnerHTML={{
                  __html: emailHtml,
                }}
              ></div> */}

              <div className="mt-3">
                <Row>
                  {attachments?.length > 0 &&
                    attachments.map((attachment, attachmentIndex) => (
                      <Col md={3}>
                        <div
                          key={attachmentIndex}
                          className="attachment mb-3 tab-content"
                        >
                          {attachment.type === "image" && (
                            <>
                              {fnImg(attachment)}
                              <div className="image-attachment">
                                <img
                                  src={`data:${
                                    attachment.mimeType
                                  };base64,${attachment.data.data
                                    .replace(/-/g, `+`)
                                    .replace(/_/g, `/`)}`}
                                  alt={attachment.name}
                                  className="image-attachment"
                                />
                                <p className="mt-2">{attachment.name}</p>
                              </div>
                              <div className="d-flex justify-content-between mt-3 align-items-center">
                                <ImageIcon />
                                <a
                                  href={`data:${
                                    attachment.mimeType
                                  };base64,${attachment.data.data
                                    .replace(/-/g, `+`)
                                    .replace(/_/g, `/`)}`}
                                  download={attachment.name}
                                >
                                  <DownloadIcon />
                                </a>
                              </div>
                            </>
                          )}
                          {attachment.type === "pdf" && (
                            <div>
                              <p className="mt-2">{attachment.name}</p>
                              <iframe
                                src={`data:application/pdf;base64,${attachment.data.data
                                  .replace(/-/g, `+`)
                                  .replace(/_/g, `/`)}`}
                                title={attachment.name}
                                width="100%"
                                height="500px"
                              />
                              <div className="d-flex justify-content-between mt-3 align-items-center">
                                <Pdficon />
                                <a
                                  href={`data:application/pdf;base64,${attachment.data.data
                                    .replace(/-/g, `+`)
                                    .replace(/_/g, `/`)}`}
                                  download={attachment.name}
                                >
                                  <DownloadIcon />
                                </a>
                              </div>
                            </div>
                          )}
                          {attachment.type === "html" && (
                            <div>
                              <div
                                dangerouslySetInnerHTML={{
                                  __html: attachment.data.data,
                                }}
                              />
                              <p className="mt-2">{attachment.name}</p>
                              <div className="d-flex justify-content-between mt-3 align-items-center">
                                <HtmlIcon />
                                <a
                                  href={`data:${attachment.mimeType};base64,${attachment.data.data}`}
                                  download={attachment.name}
                                >
                                  <DownloadIcon />
                                </a>
                              </div>
                            </div>
                          )}
                          {attachment.type === "audio" && (
                            <div>
                              <p className="mt-2">{attachment.name}</p>
                              <audio controls>
                                <source
                                  src={`data:application/pdf;base64,${attachment.data.data
                                    .replace(/-/g, `+`)
                                    .replace(/_/g, `/`)}`}
                                  type="audio/mpeg"
                                />
                                Your browser does not support the audio element.
                              </audio>
                              <div className="d-flex justify-content-between mt-3 align-items-center">
                                <AudioIcon />
                                <a
                                  href={`data:application/pdf;base64,${attachment.data.data
                                    .replace(/-/g, `+`)
                                    .replace(/_/g, `/`)}`}
                                  download={attachment.name}
                                >
                                  <DownloadIcon />
                                </a>
                              </div>
                            </div>
                          )}
                          {attachment.type === "plain" && (
                            <div>
                              <p className="mt-2">{attachment.name}</p>
                              <div className="d-flex justify-content-between mt-3 align-items-center">
                                <Textfie />
                                <a
                                  href={`data:application/pdf;base64,${attachment.data.data
                                    .replace(/-/g, `+`)
                                    .replace(/_/g, `/`)}`}
                                  download={attachment.name}
                                >
                                  <DownloadIcon />
                                </a>
                              </div>
                            </div>
                          )}
                          {attachment.type === "spreadsheet" && (
                            <div>
                              <p className="mt-2">{attachment.name}</p>
                              <div className="d-flex justify-content-between mt-3 align-items-center">
                                <Textfie />
                                <a
                                  href={`data:application/pdf;base64,${attachment.data.data
                                    .replace(/-/g, `+`)
                                    .replace(/_/g, `/`)}`}
                                  download={attachment.name}
                                >
                                  <DownloadIcon />
                                </a>
                              </div>
                            </div>
                          )}
                          {attachment.type === "json" && (
                            <div>
                              <p className="mt-2">{attachment.name}</p>
                              <div className="d-flex justify-content-between mt-3 align-items-center">
                                <Jsonfile />
                                <a
                                  href={`data:application/pdf;base64,${attachment.data.data
                                    .replace(/-/g, `+`)
                                    .replace(/_/g, `/`)}`}
                                  download={attachment.name}
                                >
                                  <DownloadIcon />
                                </a>
                              </div>
                            </div>
                          )}
                          {attachment.type === "video" && (
                            <div>
                              <p className="mt-2">{attachment.name}</p>
                              <div className="d-flex justify-content-between mt-3 align-items-center">
                                <a
                                  href={`data:application/pdf;base64,${attachment.data.data
                                    .replace(/-/g, `+`)
                                    .replace(/_/g, `/`)}`}
                                  download={attachment.name}
                                >
                                  <DownloadIcon />
                                </a>
                              </div>
                            </div>
                          )}
                          {attachment.type === "ppt" && (
                            <div>
                              <p className="mt-2">{attachment.name}</p>
                              <div className="d-flex justify-content-between mt-3 align-items-center">
                                <PPTicon />
                                <a
                                  href={`data:application/pdf;base64,${attachment.data.data
                                    .replace(/-/g, `+`)
                                    .replace(/_/g, `/`)}`}
                                  download={attachment.name}
                                >
                                  <DownloadIcon />
                                </a>
                              </div>
                            </div>
                          )}
                          {attachment.type === "odt" && (
                            <div>
                              <p>{attachment.name}</p>
                              <div className="d-flex justify-content-between mt-3 align-items-center">
                                <WordIcon />
                                <a
                                  href={`data:application/pdf;base64,${attachment.data.data
                                    .replace(/-/g, `+`)
                                    .replace(/_/g, `/`)}`}
                                  download={attachment.name}
                                >
                                  <DownloadIcon />
                                </a>
                              </div>
                            </div>
                          )}
                          {attachment.type === "other" && (
                            <div>
                              <p>
                                Unsupported attachment type:{" "}
                                {attachment.mimeType}
                              </p>
                              <div className="d-flex justify-content-between mt-3 align-items-center">
                                <Textfie />
                                <a
                                  href={`data:${attachment.mimeType};base64,${attachment.data.data}`}
                                  download={attachment.name}
                                >
                                  <DownloadIcon />
                                </a>
                              </div>
                            </div>
                          )}
                        </div>
                      </Col>
                    ))}
                </Row>
              </div>
            </div>
          ) : (
            <div className="text-center">
              <span className="visually-hidden">Loading...</span>
            </div>
          )}
        </div>
      </div>
    </>
  );
};

export default MailDetailBox;
