import React from "react";
import Container from "react-bootstrap/Container";
import Navbar from "react-bootstrap/Navbar";
import GmailLogo from "../../Assets/Images/Gmail-Logo.png";
import ProfileMask from "../../Assets/Images/profile_mask2.png";
import { Button } from "react-bootstrap";
import Image from "react-bootstrap/Image";
import Dropdown from "react-bootstrap/Dropdown";
import { useDispatch, useSelector } from "react-redux";
import { G_InfoSlice, G_TokenSlice } from "../../ReduxToolkit/UserSlice";
import { NavLink, useNavigate } from "react-router-dom";
import Profileplaceholder from "../../Assets/Images/Profileplaceholder";
import ProImg from "../../Assets/Images/proPlaceholder.jpg";
import { MaildetailSlice } from "../../ReduxToolkit/EmailSlice";
import LogOutIcon from "../../Assets/Images/LogOutIcon";

const Header = (props) => {
  const UserInfo = useSelector((state) => state.userdata.G_Info);
  let navigate = useNavigate();  
  let dispatch = useDispatch();
  function Logout() {
    dispatch(G_TokenSlice(null));
    dispatch(G_InfoSlice(null));
    localStorage.clear();
  }
  return (
    <>
      <Navbar className="bg-body-tertiary">
        <Container fluid>
          <Navbar.Brand
          className="user-select-auto"
            onClick={() => {
              // navigate("/inbox");
              dispatch(MaildetailSlice(null));
              props?.OnLogoClick("inbox");
            }}
          >
            <img
              alt={GmailLogo}
              src={GmailLogo}
              //   width="30"

              className="d-inline-block align-top logo-css"
            />
            Gmail
          </Navbar.Brand>
          <Dropdown>
            <Dropdown.Toggle variant="success" id="dropdown-basic">
              <p className="m-0"> {UserInfo?.name}</p>
              <Image
                alt={UserInfo?.picture}
                src={UserInfo?.picture}
                onError={({ currentTarget }) => {
                  currentTarget.onerror = null;
                  currentTarget.src = ProImg;
                }}
                //   width="30"

                className="d-inline-block align-top logo-css"
              />
            </Dropdown.Toggle>

            <Dropdown.Menu>
              <Dropdown.Item onClick={Logout}><LogOutIcon/> Log out</Dropdown.Item>
            </Dropdown.Menu>
          </Dropdown>
        </Container>
      </Navbar>
    </>
  );
};

export default Header;
