import {
  BrowserRouter as Router,
  Navigate,
  Route,
  Routes,
} from "react-router-dom";
import Layout from "./AllRoute/Layouts/Layout";
import "./App.css";
import 'react-toastify/dist/ReactToastify.css';
import 'react-confirm-alert/src/react-confirm-alert.css';
import LoginPage from "./AuthRoute/LoginPage";
import { useSelector } from "react-redux";
import { ToastContainer, toast } from 'react-toastify';

function App() {
  const G_Token = useSelector((state) => state.userdata.G_Token);
  const Is_Login = G_Token !== null ? true : false;

  const AllRoute = () => {
    return (
      <>
        <Route path="/*" element={<Layout />} />
      </>
    );
  };
  const AuthRoute = () => {
    return (
      <>
        <Route path="*" element={<Navigate to="/login" />} />        
        <Route path="/login" element={<LoginPage />} />
      </>
    );
  };

  return (
    <>
      <div className="main">
        {/* Wrap the Router component with BrowserRouter */}
        <ToastContainer />
        <Router 
        basename={process.env.PUBLIC_URL}
        >
          <Routes>{Is_Login ? AllRoute() : AuthRoute()}</Routes>
        </Router>
      </div>
    </>
  );
}

export default App;
