import React from "react";
import MailBox from "./Components/MailBox";
import MailDetailBox from "./Components/MailDetailBox";
import { useSelector } from "react-redux";

const Inbox = () => {
  const EmailData = useSelector((state) => state.emaildata.Maildetail);
  return (
    <>
      {EmailData !== null ? (
        <MailDetailBox EmailData={EmailData} />
      ) : (
        <MailBox labelIds="INBOX" />
      )}
    </>
  );
};

export default Inbox;
