import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  Maildetail: localStorage.getItem("Maildetail")
    ? JSON.parse(localStorage.getItem("Maildetail"))
    : null,
  AllCount: localStorage.getItem("AllCount")
    ? JSON.parse(localStorage.getItem("AllCount"))
    : null,
};

const EmailSlice = createSlice({
  name: "email",
  initialState,
  reducers: {
    MaildetailSlice: (state, action) => {
      state.Maildetail = action.payload;
      localStorage.setItem("Maildetail", JSON.stringify(action.payload));
    },
    AllCountSlice: (state, action) => {
      state.AllCount = action.payload;
      localStorage.setItem("AllCount", JSON.stringify(action.payload));
    },
  },
});

export const { MaildetailSlice, AllCountSlice } = EmailSlice.actions;
export default EmailSlice.reducer;
