import axios from "axios";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import ChildMailBox from "./ChildMailBox";
import { Button } from "react-bootstrap";
import NoMailFound from "./NoMailFound";
import Loader from "./Loader";
import { AllCountSlice } from "../../ReduxToolkit/EmailSlice";
import { Commonservice } from "../../Services/Commonservice";

const MailBox = (props) => {
  let dispatch = useDispatch();
  const G_Token = useSelector((state) => state.userdata.G_Token);
  const UserInfo = useSelector((state) => state.userdata.G_Info);
  const [MailList, setMailList] = useState({ Loading: false, data: [] });

  const [_PageToken, _setPageToken] = useState([""]);
  const [NextLastShow, setNextLastShow] = useState(true);
  async function fetchGmailData(pageToken, step) {
    setMailList({ Loading: true, data: [] });
    let PT = [..._PageToken];
    if (step.toUpperCase() === "PREV") {
      PT.pop();
      PT.pop();
    }
    try {
      const response = await axios.get(
        `https://gmail.googleapis.com/gmail/v1/users/${UserInfo?.email}/threads`,
        {
          headers: { Authorization: `Bearer ${G_Token}` },
          params: {
            labelIds: props.labelIds,
            maxResults: 50,
            pageToken: pageToken,
          },
        }
      );
      if (response?.data?.resultSizeEstimate === 0) {
        setMailList({ Loading: false, data: [] });
      }
      const newNextPageToken = response?.data?.nextPageToken;

      let IsExist = PT.filter((i) => i === newNextPageToken);
      if (IsExist !== null && IsExist.length === 0) {
        PT.push(newNextPageToken);
        _setPageToken(PT);
      } else {
        PT.push(newNextPageToken);
        _setPageToken(PT);
      }

      let L_show = PT.filter((i) => i === undefined);

      if (L_show.length > 0) {
        setNextLastShow(false);
      }

      const messages = response.data.threads;
      const emailPromises = messages.map(async (message) => {
        const emailDetails = await axios.get(
          `https://gmail.googleapis.com/gmail/v1/users/${UserInfo?.email}/threads/${message.id}`,
          {
            headers: { Authorization: `Bearer ${G_Token}` },
          }
        );
        return emailDetails.data;
      });

      const emailsWithAttachments = await Promise.all(emailPromises);

      setMailList({ Loading: false, data: emailsWithAttachments });
    } catch (error) {
      Commonservice.Logout401(error?.response?.status);
      console.error("Error fetching Gmail data:", error);
    }
  }
  useEffect(() => {
    if (UserInfo) {
      fetchGmailData("", "");
    }
  }, [UserInfo]);

  async function deleteEmail(messageId) {
    try {
      let response = await axios.post(
        `https://gmail.googleapis.com/gmail/v1/users/${UserInfo?.email}/messages/${messageId}/trash`,
        {},
        {
          headers: {
            Authorization: `Bearer ${G_Token}`,
          },
        }
      );
      if (response.status === 200) {
        // window.location.reload();
        fetchGmailData("", "");
        Promise.all([
          fetchInboxTotalCount(),
          fetchSentTotalCount(),
          fetchDraftTotalCount(),
          fetchTrashTotalCount(),
          fetchSpamTotalCount(),
          fetchStarredTotalCount(),
          fetchSnoozedTotalCount(),
        ])
          .then(
            ([
              inboxCount,
              sentCount,
              draftCount,
              trashCount,
              spamCount,
              starredCount,
              importantCount,
            ]) => {
              const AllCount = {
                inbox: inboxCount,
                sent: sentCount,
                draft: draftCount,
                trash: trashCount,
                spam: spamCount,
                starred: starredCount,
                important: importantCount,
              };
              dispatch(AllCountSlice(AllCount));
            }
          )
          .catch((error) => {
            console.error("Error fetching email counts:", error);
            // Handle error if needed
          });
      }
    } catch (error) {
      Commonservice.Logout401(error?.response?.status);
      console.error("Error deleting email:", error);
    }
  }

  async function fetchSentTotalCount() {
    try {
      const response = await axios.get(
        `https://gmail.googleapis.com/gmail/v1/users/${UserInfo.email}/threads`,
        {
          headers: { Authorization: `Bearer ${G_Token}` },
          params: {
            q: "in:sent",
          },
        }
      );

      const totalCount = response?.data?.resultSizeEstimate;
      return totalCount;
    } catch (error) {
      Commonservice.Logout401(error?.response?.status);
      console.error("Error fetching inbox total count:", error);
      return 0; // Return 0 if there's an error
    }
  }
  async function fetchDraftTotalCount() {
    try {
      const response = await axios.get(
        `https://gmail.googleapis.com/gmail/v1/users/${UserInfo.email}/threads`,
        {
          headers: { Authorization: `Bearer ${G_Token}` },
          params: {
            q: "in:draft",
          },
        }
      );

      const totalCount = response?.data?.resultSizeEstimate;
      return totalCount;
    } catch (error) {
      Commonservice.Logout401(error?.response?.status);
      console.error("Error fetching inbox total count:", error);
      return 0; // Return 0 if there's an error
    }
  }
  async function fetchTrashTotalCount() {
    try {
      const response = await axios.get(
        `https://gmail.googleapis.com/gmail/v1/users/${UserInfo.email}/threads`,
        {
          headers: { Authorization: `Bearer ${G_Token}` },
          params: {
            q: "in:trash",
          },
        }
      );

      const totalCount = response?.data?.resultSizeEstimate;
      return totalCount;
    } catch (error) {
      Commonservice.Logout401(error?.response?.status);
      console.error("Error fetching inbox total count:", error);
      return 0; // Return 0 if there's an error
    }
  }
  async function fetchSpamTotalCount() {
    try {
      const response = await axios.get(
        `https://gmail.googleapis.com/gmail/v1/users/${UserInfo.email}/threads`,
        {
          headers: { Authorization: `Bearer ${G_Token}` },
          params: {
            q: "in:spam",
          },
        }
      );

      const totalCount = response?.data?.resultSizeEstimate;
      return totalCount;
    } catch (error) {
      Commonservice.Logout401(error?.response?.status);
      console.error("Error fetching inbox total count:", error);
      return 0; // Return 0 if there's an error
    }
  }
  async function fetchStarredTotalCount() {
    try {
      const response = await axios.get(
        `https://gmail.googleapis.com/gmail/v1/users/${UserInfo.email}/threads`,
        {
          headers: { Authorization: `Bearer ${G_Token}` },
          params: {
            q: "in:starred",
          },
        }
      );

      const totalCount = response?.data?.resultSizeEstimate;
      return totalCount;
    } catch (error) {
      Commonservice.Logout401(error?.response?.status);
      console.error("Error fetching inbox total count:", error);
      return 0; // Return 0 if there's an error
    }
  }
  async function fetchSnoozedTotalCount() {
    try {
      const response = await axios.get(
        `https://gmail.googleapis.com/gmail/v1/users/${UserInfo.email}/threads`,
        {
          headers: { Authorization: `Bearer ${G_Token}` },
          params: {
            q: "in:important",
          },
        }
      );

      const totalCount = response?.data?.resultSizeEstimate;
      return totalCount;
    } catch (error) {
      Commonservice.Logout401(error?.response?.status);
      console.error("Error fetching inbox total count:", error);
      return 0; // Return 0 if there's an error
    }
  }
  async function fetchInboxTotalCount() {
    try {
      const response = await axios.get(
        `https://gmail.googleapis.com/gmail/v1/users/${UserInfo.email}/threads`,
        {
          headers: { Authorization: `Bearer ${G_Token}` },
          params: {
            q: "in:inbox",
          },
        }
      );

      const totalCount = response?.data?.resultSizeEstimate;
      return totalCount;
    } catch (error) {
      Commonservice.Logout401(error?.response?.status);
      console.error("Error fetching inbox total count:", error);
      return 0; // Return 0 if there's an error
    }
  }

  useEffect(() => {
    if (G_Token && UserInfo) {
      Promise.all([
        fetchInboxTotalCount(),
        fetchSentTotalCount(),
        fetchDraftTotalCount(),
        fetchTrashTotalCount(),
        fetchSpamTotalCount(),
        fetchStarredTotalCount(),
        fetchSnoozedTotalCount(),
      ])
        .then(
          ([
            inboxCount,
            sentCount,
            draftCount,
            trashCount,
            spamCount,
            starredCount,
            importantCount,
          ]) => {
            const AllCount = {
              inbox: inboxCount,
              sent: sentCount,
              draft: draftCount,
              trash: trashCount,
              spam: spamCount,
              starred: starredCount,
              important: importantCount,
            };
            dispatch(AllCountSlice(AllCount));
          }
        )
        .catch((error) => {
          console.error("Error fetching email counts:", error);
          // Handle error if needed
        });
    }
  }, [G_Token, UserInfo]);

  return (
    <>
      <div className="justify-content-end d-flex btn-heading pe-3 p-2">
        {_PageToken.length > 2 && (
          <Button
            id={_PageToken[_PageToken.length - 3]}
            onClick={() =>
              fetchGmailData(_PageToken[_PageToken.length - 3], "PREV")
            }
            variant="primary me-2 np-btn"
          >
            <svg
              fill="rgb(95, 99, 104)"
              width="80px"
              height="80px"
              viewBox="-78.5 0 512 512"
              xmlns="http://www.w3.org/2000/svg"
              {...props}
            >
              <title>{"left"}</title>
              <path d="M257 64L291 98 128 262 291 426 257 460 61 262 257 64Z" />
            </svg>{" "}
            {/* Previous */}
          </Button>
        )}

        {NextLastShow && (
          <>
            {_PageToken.length > 0 && (
              <Button
                // disabled
                id={_PageToken[_PageToken.length - 1]}
                onClick={() =>
                  fetchGmailData(_PageToken[_PageToken.length - 1], "NEXT")
                }
                variant="primary np-btn"
              >
                {/* Next */}
                <svg
                  fill="rgb(95, 99, 104)"
                  width="80px"
                  height="80px"
                  viewBox="-77 0 512 512"
                  xmlns="http://www.w3.org/2000/svg"
                  {...props}
                >
                  <title>{"right"}</title>
                  <path d="M98 460L64 426 227 262 64 98 98 64 294 262 98 460Z" />
                </svg>
              </Button>
            )}
          </>
        )}
      </div>
      <div className="mails">
        {MailList.Loading ? (
          <Loader />
        ) : MailList.data?.length > 0 ? (
          <>
            {MailList.data?.map((item, index) => (
              <ChildMailBox key={index} data={item} deleteEmail={deleteEmail} />
            ))}
          </>
        ) : (
          <NoMailFound />
        )}
      </div>
    </>
  );
};

export default MailBox;
