import React from "react";
import { useGoogleLogin } from "@react-oauth/google";
import axios from "axios";
import Container from "react-bootstrap/Container";
import Navbar from "react-bootstrap/Navbar";
import GmailLogo from "../Assets/Images/Gmail-Logo.png";
import RightImg from "../Assets/Images/Prakash_DPPNG.png";
import { Button } from "react-bootstrap";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import { useDispatch } from "react-redux";
import { G_TokenSlice, G_InfoSlice } from "../ReduxToolkit/UserSlice";
import GitIcon from "../Assets/Images/GitIcon";
import { Link } from "react-router-dom";

const LoginPage = () => {
  let dispatch = useDispatch();

  const SCOPES = [
    "https://mail.google.com/",
    "https://www.googleapis.com/auth/gmail.modify",
    "https://www.googleapis.com/auth/gmail.compose",
    "https://www.googleapis.com/auth/gmail.send",
  ];

  const googleLogin = useGoogleLogin({
    onSuccess: async (tokenResponse) => {
      dispatch(G_TokenSlice(tokenResponse.access_token));
      const userInfo = await axios.get(
        "https://www.googleapis.com/oauth2/v3/userinfo",
        { headers: { Authorization: `Bearer ${tokenResponse.access_token}` } }
      );
      if (userInfo && userInfo.data) {
        dispatch(G_InfoSlice(userInfo.data));
      }
    },
    onError: (errorResponse) => console.log("errorResponse :::", errorResponse),
    onDenied: () => console.log("User denied login"),
    isSignedIn: true,
    accessType: "offline",
    scope: SCOPES.join(" "),
    prompt: "consent",
  });

  return (
    <>
      <Navbar className="loginpage-header">
        <Container>
          <Navbar.Brand href="#home">
            <img
              alt="Gmail Logo"
              src={GmailLogo}
              height="30"
              className="d-inline-block align-top"
            />
            Gmail
          </Navbar.Brand>
          <Button className="Login-btn" onClick={googleLogin}>
            Sign in
          </Button>
        </Container>
      </Navbar>
      <div className="loginpage-body">
        <Container className="h-100">
          <Row className="py-4 h-100">
            <Col>
              <div className="d-flex flex-column justify-content-center h-100">
                <h1 className="mb-0">Secure, smart, and easy to use email</h1>
                <h5 className="mt-4">
                  Get more done with Gmail. Now integrated with Google Chat,
                  Google Meet, and more, all in one place.
                </h5>
                <div className="mt-4">
                  <Button className="Login-btn" onClick={googleLogin}>
                    Sign in with Your Gmail Account
                  </Button>
                </div>
              </div>
            </Col>
            <Col>
              <div className="d-flex flex-column align-items-center justify-content-center h-100">
                <img
                  alt="Nature"
                  src={RightImg}
                  height="300"
                  width="300"
                  className="d-inline-block align-top rounded-circle login-img-prakash"
                />
              </div>
            </Col>
          </Row>
        </Container>
      </div>
      <div className="loginpage-footer">
        2024 - Made by Mr. Prakash 
        <div className="spocial-icon">
        <Link target={"_blank"} to={"https://www.linkedin.com/in/pek-s-kumar-ba2636125/"}>
                <GitIcon />
              </Link>
        </div>
      </div>
    </>
  );
};

export default LoginPage;
