import React, { useState } from "react";
import { Link, Navigate, Route, Routes } from "react-router-dom";
import Drafts from "../Drafts";
import Important from "../Important";
import Inbox from "../Inbox";
import Sent from "../Sent";
import Spam from "../Spam";
import Starred from "../Starred";
import Trash from "../Trash";
import Header from "./Header";
import Sidebar from "./Sidebar";
import GitIcon from "../../Assets/Images/GitIcon";
import All from "../All";

const Layout = () => {
  const [URLfromHeaderTOSidebar, setURLfromHeaderTOSidebar] = useState();
  function OnLogoClick(data) {
    setURLfromHeaderTOSidebar(data);
  }
  return (
    <div className="main-root">
      <Header OnLogoClick={OnLogoClick} />
      <div className="d-flex ">
        <Sidebar OnLogoClick={OnLogoClick} URLfromHeaderTOSidebar={URLfromHeaderTOSidebar} />
        <div className="all-route-main">
          <Routes>
            {/* <Route path="*" element={<Navigate to="/inbox" replace />} /> */}
            <Route path="*" element={<Navigate to="/inbox" />} /> 
            <Route path="/inbox" element={<Inbox />} />
            <Route path="/starred" element={<Starred />} />
            <Route path="/sent" element={<Sent />} />
            <Route path="/drafts" element={<Drafts />} />
            <Route path="/important" element={<Important />} />
            <Route path="/spam" element={<Spam />} />
            <Route path="/trash" element={<Trash />} />
            <Route path="/all" element={<All />} />
          </Routes>
          <footer>
            2024 - Made by Mr. Prakash
            <div className="spocial-icon">
              <Link target={"_blank"} to={"https://www.linkedin.com/in/pek-s-kumar-ba2636125/"}>
                <GitIcon />
              </Link>
            </div>
          </footer>
        </div>
      </div>
    </div>
  );
};

export default Layout;
