import moment from "moment";
import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { MaildetailSlice } from "../../ReduxToolkit/EmailSlice";
import { Commonservice } from "../../Services/Commonservice";
import { confirmAlert } from "react-confirm-alert";
import AttachmentIcon from "../../Assets/Images/AttachmentIcon";
import axios from "axios";
import DeleteIcon from "../../Assets/Images/DeleteIcon";

const ChildMailBox = (props) => {  
  const [UrlCheck, setUrlCheck] = useState(false);
  const UserInfo = useSelector((state) => state.userdata.G_Info);
  let dispatch = useDispatch();
  let Subject = props?.data?.messages[0]?.payload?.headers?.find(
    (f) => f.name === "Subject"
  );

  let From = props?.data?.messages[0]?.payload?.headers?.find(
    (f) => f.name === "From"
  );
  const getEmailSender = (value) => {
    const startIndex = value?.indexOf("<");
    const endIndex = value?.indexOf(">");
    if (startIndex !== -1 && endIndex !== -1) {
      return value?.substring(0, startIndex)?.trim();
    } else {
      return value.trim();
    }
  };
  let DateTime = props?.data?.messages[0]?.payload?.headers?.find(
    (f) => f.name === "Date"
  );

  let UnRead = props?.data?.messages[0]?.labelIds?.find((f) => f === "UNREAD");

  const handleDelete = () => {
    props.deleteEmail(props.data.id);
  };

  useEffect(() => {
    if (window.location.href.includes("trash")) {
      setUrlCheck(true);
    }
  }, []);

  function fnSelectMailDetail(data) {
    let temp = {
      ...data,
      subject: Subject,
    };
    dispatch(MaildetailSlice(temp));
  }

  return (
    <div
      // class={UnRead !== undefined ? "unread-mail mail" : "mail"}
      className="mail"
    >
      <span
        class="name false  "
        onClick={() => fnSelectMailDetail(props.data)}
        // onClick={() => dispatch(MaildetailSlice(props.data))}
      >
        <p className="m-0"> {getEmailSender(From?.value)}</p>
      </span>
      <div
        class="messageline"
        onClick={() => fnSelectMailDetail(props.data)}
        // onClick={() => dispatch(MaildetailSlice(props.data))}
      >
        <span class="subject false">{Subject?.value}</span>
        &nbsp;-&nbsp;
        <span class="message">{props?.data?.messages[0]?.snippet}</span>
      </div>

      <span class="name-1 false  d-flex">
        <p className="m-0">
          {Commonservice.getDateTimeFormatMailList(DateTime.value)}
        </p>
        <div>
          {props.data?.messages[0]?.payload?.mimeType === "multipart/mixed" ? (
            <AttachmentIcon />
          ) : (
            <></>
          )}
          {!UrlCheck && (
            <span
              className="ms-1"
              onClick={() => {
                confirmAlert({
                  title: "Confirm to Delete?",
                  message: "Are you sure to do this.",
                  buttons: [
                    {
                      label: "Yes",
                      onClick: handleDelete,
                    },
                    {
                      label: "No",
                    },
                  ],
                });
              }}
            >
              <DeleteIcon />
            </span>
          )}
        </div>
      </span>
    </div>
  );
};

export default ChildMailBox;
