import axios from "axios";
import React, { useState } from "react";
import Button from "react-bootstrap/Button";
import FloatingLabel from "react-bootstrap/FloatingLabel";
import Form from "react-bootstrap/Form";
import Modal from "react-bootstrap/Modal";
import { useSelector } from "react-redux";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { Commonservice } from "../Services/Commonservice";

const SendMail = (props) => {
  const [EmailLoader, setEmailLoader] = useState(false);
  const UserInfo = useSelector((state) => state.userdata.G_Info);
  const G_Token = useSelector((state) => state.userdata.G_Token);
  const [input, setInput] = useState({
    to: "",
    subject: "",
    comment: "",
    errors: {
      to: "",
      subject: "",
      comment: "",
      ValidationRules: [
        {
          FieldName: "to",
          ValidationType: "required",
          ValidationMessage: "This Field is a required",
        },
        {
          FieldName: "subject",
          ValidationType: "required",
          ValidationMessage: "This Field is a required",
        },

        {
          FieldName: "to",
          ValidationType: "email",
          ValidationMessage: "Invalid email address format",
        },
      ],
    },
  });

  const fnSendEmail = async (e) => {
    e.preventDefault();
    let obj = Commonservice.fnCheckValidationOfObject(input);
    setInput({
      ...obj.obj,
    });
    if (obj.isValid) {
      setEmailLoader(true);
      try {
        var encodedMessage = btoa(
          "From: " +
            UserInfo.email +
            "\r\n" +
            "To: " +
            input.to +
            "\r\n" +
            "Subject: " +
            input.subject +
            "\r\n\r\n" +
            input.comment
        )
          .toString("base64")
          .replace(/\+/g, "-")
          .replace(/\//g, "_");

        const response = await axios.post(
          `https://gmail.googleapis.com/gmail/v1/users/${UserInfo.email}/messages/send`,
          {
            raw: encodedMessage,
          },
          {
            headers: {
              Authorization: `Bearer ${G_Token}`,
              "Content-Type": "application/json",
            },
          }
        );
        if (response.status === 200) {
          setEmailLoader(false);
          props.onHide();
          toast.success("You have sent email successfully!");
        }
      } catch (error) {
        Commonservice.Logout401(error?.response?.status);
        console.error("Error sending email:", error);
      }
    }
  };

  return (
    <Modal className="emailModal" size="lg" {...props}>
      <Modal.Header closeButton>
        <Modal.Title>New Message</Modal.Title>
      </Modal.Header>
      <Form onSubmit={(e) => fnSendEmail(e)}>
        <Modal.Body>
          <Form.Control
            type="email"
            placeholder="Recipients"
            value={input.to}
            onChange={(e) =>
              setInput((prevInput) => ({
                ...prevInput,
                to: e.target.value,
                errors: {
                  ...prevInput.errors,
                  to: "", // Clear previous error when user starts typing
                },
              }))
            }
            isInvalid={input.errors.to}
          />
          {input.errors.to && (
            <Form.Control.Feedback type="invalid">
              {input.errors.to}
            </Form.Control.Feedback>
          )}
          {/* Other input fields for subject and comment */}
          <Form.Control
            type="text"
            placeholder="Subject"
            value={input.subject}
            onChange={(e) =>
              setInput((prevInput) => ({
                ...prevInput,
                subject: e.target.value,
                errors: {
                  ...prevInput.errors,
                  subject: "", // Clear previous error when user starts typing
                },
              }))
            }
            isInvalid={input.errors.subject}
          />
          {input.errors.subject && (
            <Form.Control.Feedback type="invalid">
              {input.errors.subject}
            </Form.Control.Feedback>
          )}

          {/* Comment input field */}
          <Form.Control
            as="textarea"
            placeholder="Leave a comment here"
            style={{ height: "300px" }}
            value={input.comment}
            onChange={(e) =>
              setInput((prevInput) => ({
                ...prevInput,
                comment: e.target.value,
                errors: {
                  ...prevInput.errors,
                  comment: "", // Clear previous error when user starts typing
                },
              }))
            }
            isInvalid={input.errors.comment}
          />
          {input.errors.comment && (
            <Form.Control.Feedback type="invalid">
              {input.errors.comment}
            </Form.Control.Feedback>
          )}
        </Modal.Body>
        <Modal.Footer>
          <Button type="submit" variant="primary">
            {EmailLoader ? "Loading..." : "Send"}
          </Button>
        </Modal.Footer>
      </Form>
    </Modal>
  );
};

export default SendMail;
